<template>
  <footer>
    <div class="container">
      <p>Copyright1999-<span>{{ year }}</span> .All Rights Reserved</p>
      <p>400-6300-999</p>
      <p>
        ICP10218183 ICP161188 11010802020593 130052
      </p>
    </div>
  </footer>
</template>

<script>
  export default {
    data (){
      return {
        year : 0
      }
    },
    methods : {
      now_year (){
        let that = this
        let date = new Date();
        let now_year = date.getFullYear();
        that.year = now_year
      }
    },
    created () {
      let that = this
      that.now_year()
    }
  }
</script>

<style lang="css">
  footer{background: #3b3b3b;padding: 30px 0;color: #fff;font-size: 12px; text-align: center;
    line-height: 24px;}

</style>



