<template>
  <div class="banner" id="banner">
    <div class="container">
      <img src="./../assets/banner01.jpg" alt="">
      <img src="./../assets/banner02.jpg" alt="">
      <img src="./../assets/banner03.jpg" alt="">
      <div class="banTxt">
        <h1></h1>
        <h3></h3>
        <span></span>
        <p>2019 <br> <br></p>
        <a id="more" @click="moveToLesson()" class="button"></a>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {

      }
    },
    methods:{
      moveToLesson (){
        const currentY = document.documentElement.scrollTop || document.body.scrollTop
        const moveToY = document.getElementById('banner').clientHeight + document.getElementsByTagName('header')[0].clientHeight
        this.$options.methods.moveAnimation(currentY,moveToY)
      },
      moveAnimation (currentY,moveToY){
        let needScrollTop = moveToY - currentY
        let _currentY = currentY
        setTimeout(() => {
          const dist = Math.ceil(needScrollTop / 10)
          _currentY += dist
          window.scrollTo(_currentY, currentY)
          if (needScrollTop > 10 || needScrollTop < -10) {
            this.moveAnimation(_currentY, moveToY)
          } else {
            window.scrollTo(_currentY, moveToY)
          }
        }, 5)
      }
    }
  }
</script>

<style scoped>
  .banner{
    background: url(./../assets/bannerBg.jpg) center top no-repeat;}
  .banner .container{
    position: relative;}
  .banner img{display: block;
    pointer-events: none;
    width: 100%;}
  .banTxt{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding-top: 115px;
    color: #0037b6;}
  h1{
    font-size: 72px;
    color: #0037b6;
    font-weight: bold;}
  h3{
    font-size: 42px;color: #0037b6;
    font-weight: bold;}
  span{
    display: block;
    width: 80px;
    height: 9px;
    margin: 20px 0 30px;
    background-color: #0037b6;}
  p{
    font-size: 16px;
    color: #666666;
    line-height: 30px;}
  .button{
    display: block;
    width: 180px;
    height: 66px;
    border: 1px solid #0037b6;
    color: #0037b6;text-align: center;
    line-height: 64px;
    font-size: 24px;
    font-weight: bold;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    margin-top: 35px;transition: .5s;
    cursor: pointer;}
  .button:hover{
    background-color: #0037b6;
    color: #fff;}
</style>



